@charset "UTF-8";
/* -------------------
  COMMON
--------------------*/

body {
  font-family: $font-base;
  font-weight: 500;
  &.fixed {
    height: 100vh;
    overflow: hidden;
    // display: flex;
  }
}

// =========================================
// CONTROL VIEW ELEMENT
// =========================================

.pc-only {
  display: block;

  @include mq() {
    display: none !important;
  }
}

.pc-only2 {
  display: block;

  @include mq(sm2) {
    display: none !important;
  }
}

.pc-onlyt {
  display: block;

  @include mq(smt) {
    display: none !important;
  }
}

.pc-only3 {
  display: inline-block;

  @include mq() {
    display: none !important;
  }
}

.sp-only {
  display: none !important;

  @include mq() {
    display: block !important;
  }
}

.sp-only2 {
  display: none !important;

  @include mq(sm2) {
    display: block !important;
  }
}

.sp-onlyt {
  display: none !important;

  @include mq(smt) {
    display: block !important;
  }
}

