.news-inner .r-content {
  padding-bottom: 20px;
  padding-right: 30px;
  margin-left: 0;
  border-top: 1px solid #215498;
}

.news-inner .r-content .section-ttl {
  display: flex;
  margin-top: -35px;
}

.news-inner .r-content .section-ttl span {
  background-color: white;
}

@media screen and (max-width: 576px) {
  .news-inner .r-content {
    padding-right: 5px;
    margin-left: -15px;
    margin-right: -5px;
  }
  .news-inner .r-content .section-ttl span {
    padding-left: 10px;
  }
}

.news .category {
  display: flex;
  justify-content: flex-end;
  margin: 13px 0;
}

.news .category-list {
  display: flex;
}

.news .category-list li {
  padding: 8px;
  color: #4F157F;
  background-color: #EFEFEF;
  text-align: center;
}

.news .category-list li:not(:last-child) {
  margin-right: 1px;
}

.news .category-list li:hover {
  background-color: #e5e2e2;
}

.news .category-list li.active {
  background-color: #4F157F;
  color: white;
}

.news .category-list li a {
  font-size: 14px;
}

@media screen and (max-width: 576px) {
  .news .category-list li {
    padding: 5px 8px;
  }
}

@media screen and (max-width: 576px) {
  .news .category {
    margin-top: 42px;
    margin-bottom: 10px;
  }
}

.news-item {
  background-color: #fff;
  transition: all .25s;
}

.news-item:hover {
  background-color: #f2f2f2;
}

.news-item--inner {
  position: relative;
  display: flex;
  padding: 20px 0;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}

@media screen and (max-width: 576px) {
  .news-item--inner {
    flex-flow: column;
  }
}

.news-item--dateWrap {
  display: flex;
  align-items: center;
  margin-right: 30px;
}

.news-item--dateWrap .date {
  margin-right: 25px;
}

@media screen and (max-width: 576px) {
  .news-item--dateWrap {
    margin-right: 0;
    margin-bottom: 15px;
  }
}

.news-item::after {
  display: block;
  content: '';
  width: 30px;
  height: 1px;
  background-color: #4F157F;
  margin-top: -1px;
}

.news-item .desc {
  -webkit-box-orient: vertical;
  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 1;
}

@media screen and (max-width: 576px) {
  .news-item .desc {
    -webkit-line-clamp: 2;
  }
}

.news .btn-detail {
  margin-top: 33px;
  margin-bottom: 0;
  padding-left: 50px;
  max-width: 350px;
  height: 60px;
  border: 1px solid #4F157F;
  border-bottom-width: 4px;
}

.news .btn-detail::after {
  width: 9px;
  height: 15px;
  background-image: url(../images/common/icon-arrow-right-main.svg);
}

.p-bottom {
  margin-bottom: 240px;
  position: relative;
}

.p-bottom .p-btn {
  width: 100%;
  padding-top: 24px;
  padding-bottom: 20px;
  color: white;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.p-bottom .p-btn::after {
  display: inline-block;
  content: '';
  width: 9px;
  height: 15px;
  position: absolute;
  right: 20px;
  background-image: url(../images/common/icon-arrow-right-white.svg);
}

.p-bottom .p-btn p {
  font-size: 0.875rem;
}

.p-bottom .p-btn h2 {
  font-size: 1.4375rem;
  margin-top: 5px;
}

.p-bottom .col2-item {
  margin-bottom: 0;
}

.p-bottom .col2-item:nth-child(1) .p-btn {
  background-color: #3C50A0;
}

.p-bottom .col2-item:nth-child(2) .p-btn {
  background-color: #3C965A;
}

@media screen and (max-width: 767px) {
  .p-bottom .col2-item:first-child {
    margin-bottom: 30px;
  }
}

.p-bottom .p-bg-pane {
  background-color: #4F157F;
  position: absolute;
  top: 60px;
  bottom: -90px;
  width: 100%;
  z-index: -1;
}

@media screen and (min-width: 1200px) {
  .p-bottom .p-bg-pane {
    width: calc(100% - 160px);
    left: 50%;
    transform: translateX(-50%);
  }
}

.p-bottom .p-bg-pane .line-list {
  position: absolute;
  bottom: 0;
}

.p-bottom .p-bg-pane .line-list .line {
  height: 15px;
  border-top: 3px solid;
}

.p-bottom .p-bg-pane .line-list .line:nth-child(1) {
  width: 21.42857vw;
  border-top-color: #215498;
}

.p-bottom .p-bg-pane .line-list .line:nth-child(2) {
  width: 28.57143vw;
  border-top-color: #3C965A;
}

.p-bottom .p-bg-pane .line-list .line:nth-child(3) {
  width: 35.71429vw;
  border-top-color: #F2879B;
}

.p-bottom .p-bg-pane .container {
  position: absolute;
  left: 50%;
  transform: translate(-50%, -50%);
  top: 0;
}

@media screen and (max-width: 767px) {
  .p-bottom {
    margin-bottom: 200px;
  }
  .p-bottom .p-btn {
    padding-top: 20px;
    padding-bottom: 17px;
  }
  .p-bottom .p-btn p {
    font-size: 0.8125rem;
  }
  .p-bottom .p-btn h2 {
    font-size: 1.125rem;
  }
  .p-bottom .p-bg-pane {
    bottom: -75px;
  }
  .p-bottom .p-bg-pane .line-list .line:nth-child(1) {
    width: 40vw;
  }
  .p-bottom .p-bg-pane .line-list .line:nth-child(2) {
    width: 69.33333vw;
  }
  .p-bottom .p-bg-pane .line-list .line:nth-child(3) {
    width: 96vw;
  }
}
